<template>
	<div id="order-op">
		<public-header :title="pageInfo[type]['page_title']"></public-header>
		<div class="content">
			<template v-if="type == 'direct_transfer'">
				<div class="padding-15-lr padding-10-tb font-14 c-gray">选择配送员</div>
				<van-radio-group v-model="itemSelectedKey">
					<van-cell-group>
						<van-cell :title="item.title" clickable @click="onSelectItem(index)" class="no-fix" v-for="(item, index) in resultData" :key="index">
							<van-radio :name="index" />
						</van-cell>
					</van-cell-group>
				</van-radio-group>
			</template>
			<template v-else>
				<div class="padding-15-lr padding-10-tb font-14 c-gray">{{type == 'delivery_cancel' ? '取消订单原因' : '转单原因'}}</div>
				<van-radio-group v-model="itemSelectedKey">
					<van-cell-group>
						<van-cell :title="item" clickable @click="onSelectItem(index)" class="no-fix" v-for="(item, index) in resultData" :key="index">
							<van-radio :name="index" />
						</van-cell>
						<van-cell title="其他" clickable class="no-fix" @click="onSelectItem(-1)">
							<van-radio name="-1" />
						</van-cell>
						<van-field
							v-model="reason"
							type="textarea"
							placeholder="请输入原因"
							rows="5"
							v-if="itemSelectedKey == -1"
						/>
					</van-cell-group>
				</van-radio-group>
			</template>
			<div class="padding-15">
				<van-button size="normal" block class="bg-primary font-16" @click='onSubmit'>提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				showPreLoading: true,
				resultData: {},
				itemSelectedKey: 0,
				reason: '',
				pageInfo: {
					delivery_cancel: {
						page_title: '取消订单理由',
					},
					direct_transfer: {
						page_title: '定向转单',
					},
					delivery_transfer: {
						page_title: '转单',
					}
				}
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/order/takeout/op',
					data: {
						id: that.order_id,
						type: that.type
					},
					success: function(res) {
						that.resultData = res;
					}
				});
			},
			onSelectItem(index) {
				this.itemSelectedKey = index;
			},
			onSubmit() {
				if(this.type == 'direct_transfer') {
					var deliveryer_id = this.resultData[this.itemSelectedKey].id;
					if(!deliveryer_id) {
						this.util.$toast('请选择要转单到的配送员', '', 1000);
						return false;
					}
				} else {
					if(this.itemSelectedKey != -1) {
						this.reason = this.resultData[this.itemSelectedKey];
					} 
					if(!this.reason) {
						this.util.$toast('请先输入原因', '', 1000);
						return false;
					}
				}
				var params = {
					url: 'delivery/order/takeout/status',
					confirm: '',
					data: {
						id: this.order_id,
						type: this.type,
						deliveryer_id: deliveryer_id,
						reason: this.reason
					},
					successUrl: this.util.getUrl({path: 'pages/order/takeout', query: {status: this.order_status}}),
				};
				this.util.jspost(params);
			}
		},
		mounted() {
			this.onLoad();
		},
		created(){
			this.query = this.$route.query;
			if(this.query) {
				this.order_id = this.query.order_id;
				this.order_status = this.query.order_status;
				this.type = this.query.type;
			}
		}
	}
</script>

<style>
	#order-op .van-field{
		padding-top: 0px;
	}
	#order-op .van-field textarea{
		background-color: #f5f5f5;
		padding: 10px;
	}
</style>


